function readdClass(el, classname) {
  el.removeClass(classname)
  var newone = el.clone(true);
  el.before(newone);
  el.remove()
}

class RecUnit extends Component {
  constructor() {
    super();
    this.state = {

    }

    this.like = this.like.bind(this)
  }

  like(e) {

    const component = this
   var element = e.currentTarget
   $(element).removeClass('cardpressed')
    void element.offsetWidth;
    $(element).addClass('cardpressed')

    readdClass($(element).find(".iloveittext"), 'iloveittext-pressed')
    readdClass($(element).find(".iloveittextbackground"), 'iloveittextbackround-pressed')
    var iloveittext = $(element).find(".iloveittext")
    $(iloveittext).removeClass('iloveittext-pressed')
    void iloveittext.offsetWidth;
    $(iloveittext).addClass('iloveittext-pressed')

    var iloveittextbackground = $(element).find(".iloveittextbackground")
    $(iloveittextbackground).removeClass('iloveittextbackround-pressed')
    void iloveittextbackground.offsetWidth;
    $(iloveittextbackground).addClass('iloveittextbackround-pressed')

    firebase.database().ref('/publicrecs/' + this.props.fbid + '/rating').once('value').then(function(snapshot) {
  
      console.log(snapshot.val())
      var rating = snapshot.val()

      firebase.database().ref('/publicrecs/' + component.props.fbid).update({'rating':rating + 1})


    })

    //$(element).find(".iloveittext").addClass('iloveittext-pressed')
    //$(element).find(".iloveittextbackground").addClass('iloveittextbackround-pressed')

  }

  render() {

     const hrefattr = this.props.link ? {href:this.props.link} : {}
    return (
      <div className="card" onClick={this.like}><div className="row">

        <div className="col-sm-8">
        <h1><a {...hrefattr}>{this.props.title}</a></h1>
        <h2 className="subtitle"><span className="material-icons">{typedict[this.props.type]}</span>{this.props.type}</h2></div>
        </div>
        
<div className="row">
        <p>{this.props.description}</p></div>
        <div className="iloveittext">i love it!</div>
        <div className="iloveittextbackground"></div>
      </div>
    );
  }
}

class Form extends Component {
  constructor() {
    super();
    this.state = {
      title: '',
      link: '',
      linkvalid: true,
      type: '',
      description: '',
      buttonColor: "lightgray",
    }
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.radioChange = this.radioChange.bind(this);
    this.checkLink = this.checkLink.bind(this);
    this.handleCancel = this.handleCancel.bind(this);


  }

  radioChange(e) {
    this.setState({
      type: e.currentTarget.value
    });
  }

  checkLink(e) {

  }

  handleChange(e) {

    this.setState({
      [e.target.name]: e.target.value
    });

    if (e.target.name == "link" && e.target.value !== '') {
      if (validate(e.target.value)) {
        console.log(this.linkvalid)
        this.setState({
          linkvalid:true
        });
      } else {
        this.setState({
          linkvalid:false
        });
        console.log(this.linkvalid)
      }
    } else if (e.target.name == "link" && e.target.value === '') {
      this.setState({
        linkvalid:true
      });
    }

  }

  handleSubmit(e) {
    e.preventDefault();

      const itemsRef = firebase.database().ref('publicrecs');
      const item = {
        title: this.state.title,
        link: (this.state.linkvalid ? this.state.link : ''),
        creator: "",
        rating: 0,
        description: this.state.description,
        type: this.state.type
      }
      itemsRef.push(item);
      this.setState({
        title: '',
        link: '',
        description:'',
      type:''     
     });
    
    this.props.addModalSuccess()
    this.props.getCurrentRecs()
  }

  handleCancel(e) {
    e.preventDefault();

    this.setState({
      title: '',
      link: '',
      description:'',
    type:''     
   });

   this.props.addModalHide()

  }

  render() {
    return (
      <div>
      <section className="add-item">
  <form>
    <h2>NAME</h2>
    <span><input type="text" name="title" placeholder="title" onChange={this.handleChange} value={this.state.title} /></span>
    <h2>LINK?    <span style={{ color: this.state.linkvalid ? 'white': 'lightgray'}}>   THIS LINK WON't WORK, but that's ok.</span></h2>
    <input className="linkinput"  type="text" name="link" placeholder="link" onChange={this.handleChange} value={this.state.link} />
    <div className="typeselector">
        {Object.keys(typedict).map(typeval => <div className="typeradio"><input id={typeval} checked={this.state.type === typeval} type="radio" key={typeval} value={typeval} onChange={this.radioChange.bind(this)}/><label for={typeval} class="radio" className="button"><span className='material-icons'>{typedict[typeval]}</span> {typeval.toUpperCase()}</label></div>)} 
        </div> 
        <h2>WHAT'S THE DEAL</h2>
    <textarea name="description" onChange={this.handleChange} value={this.state.description} />
          <div className="modalbuttons">
      <button onClick={this.handleCancel} key="cancel button" className="cancelbutton">cancel</button>
      <button onClick={this.handleSubmit} className={"submitbutton " + (this.state.title.trim() && this.state.type.trim() && this.state.description.trim() ? 'submitshake' : '')}>REC.</button></div>
  </form>
  
</section> </div>
    )
  }
}


